import * as Event from '@/code/custom-event.js'
import EventBus from '@/utils/event-bus.js'

let bgUrl = null
function savePic (url) {
  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')
  let img = new Image()
  img.crossOrigin = 'Anonymous'
  img.onload = function () {
    if (url === bgUrl) {
      canvas.width = img.width
      canvas.height = img.height
      ctx.drawImage(img, 0, 0)
      EventBus.$emit(Event.SAVE_PIC_SUCCESS, url)
      try {
        window.localStorage.setItem('smart_campus__bg', canvas.toDataURL('image/jpg'))
        log(`bg load success: ${ url }`)
      } catch (e) {
        log(`bg save failed, ${ url }: ${ e.message }`)
      }
    }
    canvas = null
    ctx = null
    img = null
  }
  img.onerror = function () {
    if (url === bgUrl) {
      bgUrl = null
      log(`bg load failed: ${ url }`)
    }
    canvas = null
    ctx = null
    img = null
  }
  img.onabort = function () {
    if (url === bgUrl) {
      bgUrl = null
      log(`bg load abort: ${ url }`)
    }
    canvas = null
    ctx = null
    img = null
  }
  img.src = url
}

export function listen () {
  EventBus.$on(Event.SAVE_PIC, url => {
    if (bgUrl !== url) {
      bgUrl = url
      savePic(url)
    }
  })
}
